(0, 0, 0, 0.6);\n$selected-field-border: #e6f5fe;\n$table-row-hover: #f0f1f2;\n\n@mixin form-builder-mat-icon {\n  margin-left: auto;\n  display: flex !important;\n  align-items: center;\n  justify-content: center;\n}\n//\n\n$border-gray: #d5d8db;\n$color-red: #e60404;\n$color-red-dark: #ad1b1b;\n$color-red-light: #ffcdd2;\n$color-gray: #586069;\n$color-gray-light: #f7f8f8;\n$color-white: #ffffff;\n$color-black: #000000;\n$color-primary: #048be6;\n$color-primary-dark: #005797;\n$color-primary-light: #def2fe;\n$color-accent: #037dd1;\n$color-border-light: #e0e0e0;\n$color-gradient: linear-gradient(180deg, $color-white 0%, $color-primary-light 78.12%, $selected-field-border 100%);\n\n.color-red {\n  color: $color-red !important;\n}\n.color-red-dark {\n  color: $color-red-dark !important;\n}\n.color-red-light {\n  color: $color-red-light !important;\n}\n.color-gray {\n  color: $color-gray !important;\n}\n.color-gray-light {\n  color: $color-gray-light !important;\n}\n.color-white {\n  color: $color-white !important;\n}\n.color-black {\n  color: $color-black !important;\n}\n.color-border-light {\n  color: $color-border-light !important;\n}\n.color-primary {\n  color: $color-primary !important;\n}\n.color-primary-dark {\n  color: $color-primary-dark !important;\n}\n.color-primary-light {\n  color: $color-primary-light !important;\n}\n.color-accent {\n  color: $color-accent !important;\n}\n\n.background-red {\n  background-color: $color-red !important;\n}\n.background-red-dark {\n  background-color: $color-red-dark !important;\n}\n.background-red-light {\n  background-color: $color-red-light !important;\n}\n.background-gray {\n  background-color: $color-gray !important;\n}\n.background-gray-light {\n  background-color: $color-gray-light !important;\n}\n.background-white {\n  background-color: $color-white !important;\n}\n.background-black {\n  background-color: $color-black !important;\n}\n.background-border-light {\n  background-color: $color-border-light !important;\n}\n.background-primary {\n  background-color: $color-primary !important;\n}\n.background-primary-dark {\n  background-color: $color-primary-dark !important;\n}\n.background-primary-light {\n  background-color: $color-primary-light !important;\n}\n.background-accent {\n  background-color: $color-accent !important;\n}\n\n.background-gradient {\n  background: $color-gradient !important;\n}\n","@import 'global-style-helpers/colors';\n@import 'global-style-helpers/cursor';\n@import 'global-style-helpers/display';\n@import 'global-style-helpers/material';\n@import 'global-style-helpers/xenon';\n\nhtml,\nbody {\n  height: 100%;\n  background: $color-gradient;\n}\nbody {\n  margin: 0;\n  font-family: Roboto, 'Helvetica Neue', sans-serif;\n}\n\ntable {\n  width: 100%;\n}\n\n.task-form-backdrop {\n  top: 56px;\n  bottom: 0;\n  right: 0;\n  left: 0;\n  background: $color-gradient;\n}\n.task-form-panel {\n  max-height: calc(100vh - 76px);\n  min-height: calc(100vh - 76px);\n  width: 1024px;\n  top: 0;\n}\n\n// x-form-builder styles\n@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,600;1,700&display=swap');\n\n* {\n  font-family: 'Montserrat', sans-serif;\n}\nhtml,\nbody {\n  height: 100%;\n}\nbody {\n  margin: 0;\n}\n\nbutton {\n  &:focus {\n    outline: none;\n  }\n}\n\n.inline-block {\n  display: inline-block !important;\n}\n.dash {\n  border-bottom: 2px solid rgba(0, 0, 0, 0.6);\n  width: 10px;\n  display: inline-block;\n  margin: 0 10px;\n  height: 11px;\n  margin-bottom: 3px;\n}\n\n.border-bottom {\n  border-bottom: 1px solid $table-row-hover;\n}\n\n.border-bottom-dark {\n  border-bottom: 1px solid #a3a9b0;\n}\n.cdk-global-overlay-wrapper {\n  justify-content: center;\n  align-items: center;\n}\n\n.drive-file-dialog {\n  width: 60%;\n}\n\n.two-column-row {\n  display: flex;\n\n  & > * {\n    flex: 1;\n  }\n  & > *:first-child {\n    margin-right: 20px;\n  }\n}\n",".clickable {\n  cursor: pointer !important;\n}\n.draggable {\n  cursor: move !important;\n}\n",".display-none {\n  display: none;\n}\n\n.display-block {\n  display: block;\n}\n\n.display-inline-block {\n  display: inline-block;\n}\n\n.hidden {\n  visibility: hidden;\n}\n\n.overflow-hidden {\n  overflow: hidden;\n}\n\n.overflow-y-hidden {\n  overflow-y: hidden;\n}\n\n.overflow-x-hidden {\n  overflow-x: hidden;\n}\n\n.overflow-auto {\n  overflow: auto;\n}\n\n.overflow-y-auto {\n  overflow-y: auto;\n}\n\n.overlfow-x-auto {\n  overflow-x: auto;\n}\n","@import 'colors';\nbutton.mat-flat-button {\n  border-radius: 24px;\n  font-family: 'Montserrat', sans-serif !important;\n  font-weight: normal !important;\n  font-size: 14px !important;\n  line-height: 20px !important;\n  letter-spacing: 1.25px !important;\n  text-transform: uppercase !important;\n}\nbutton.mat-flat-button.mat-primary:hover:not(.mat-button-disabled) {\n  background-color: $color-accent;\n}\nbutton.mat-raised-button {\n  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 1px 5px rgba(0, 0, 0, 0.2);\n  border-radius: 24px;\n  font-family: 'Montserrat', sans-serif !important;\n  font-weight: normal !important;\n  font-size: 14px !important;\n  line-height: 20px !important;\n  letter-spacing: 1.25px !important;\n  text-transform: uppercase !important;\n}\nbutton.mat-raised-button:hover:not(.mat-button-disabled) {\n  background-color: $color-accent;\n}\nbutton.mat-stroked-button {\n  border-radius: 24px;\n  font-family: 'Montserrat', sans-serif !important;\n  font-weight: normal !important;\n  font-size: 14px !important;\n  line-height: 20px !important;\n  letter-spacing: 1.25px !important;\n  text-transform: uppercase !important;\n}\nbutton.mat-stroked-button:hover:not(.mat-button-disabled) {\n  border: 1px solid $color-primary;\n  box-sizing: border-box;\n  border-radius: 24px;\n}\nbutton.mat-button {\n  border-radius: 0px;\n  font-family: 'Montserrat', sans-serif !important;\n  font-weight: normal !important;\n  font-size: 14px !important;\n  line-height: 30px !important;\n  letter-spacing: 1.25px !important;\n  text-transform: uppercase !important;\n}\n\nmat-accordion mat-expansion-panel {\n  margin: 0;\n  box-shadow: none !important;\n  .mat-expansion-panel-header.mat-expanded {\n    height: 48px;\n  }\n  .mat-expansion-panel-body {\n    padding: 0 !important;\n  }\n}\n.mat-expansion-panel-spacing {\n  margin: 0px !important;\n}\n","// File for which all imports are resolved and bundled. This is the entry-point for\n// the `@angular/material` theming Sass bundle. See `//src/material:theming_bundle`.\n\n// Import all the theming functionality.\n// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: 1000 !default;\n$cdk-z-index-overlay: 1000 !default;\n$cdk-z-index-overlay-backdrop: 1000 !default;\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.32) !default;\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container, .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // The container should be the size of the viewport.\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n  }\n\n  // The overlay-container is an invisible element which contains all individual overlays.\n  .cdk-overlay-container {\n    position: fixed;\n    z-index: $cdk-z-index-overlay-container;\n\n    &:empty {\n      // Hide the element when it doesn't have any child nodes. This doesn't\n      // include overlays that have been detached, rather than disposed.\n      display: none;\n    }\n  }\n\n  // We use an extra wrapper element in order to use make the overlay itself a flex item.\n  // This makes centering the overlay easy without running into the subpixel rendering\n  // problems tied to using `transform` and without interfering with the other position\n  // strategies.\n  .cdk-global-overlay-wrapper {\n    display: flex;\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  // A single overlay pane.\n  .cdk-overlay-pane {\n    // Note: it's important for this one to start off `absolute`,\n    // in order for us to be able to measure it correctly.\n    position: absolute;\n    pointer-events: auto;\n    box-sizing: border-box;\n    z-index: $cdk-z-index-overlay;\n\n    // For connected-position overlays, we set `display: flex` in\n    // order to force `max-width` and `max-height` to take effect.\n    display: flex;\n    max-width: 100%;\n    max-height: 100%;\n  }\n\n  .cdk-overlay-backdrop {\n    // TODO(jelbourn): reuse sidenav fullscreen mixin.\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n\n    z-index: $cdk-z-index-overlay-backdrop;\n    pointer-events: auto;\n    -webkit-tap-highlight-color: transparent;\n    transition: opacity $backdrop-animation-duration $backdrop-animation-timing-function;\n    opacity: 0;\n\n    &.cdk-overlay-backdrop-showing {\n      opacity: 1;\n\n      // In high contrast mode the rgba background will become solid so we need to fall back\n      // to making it opaque using `opacity`. Note that we can't use the `cdk-high-contrast`\n      // mixin, because we can't normalize the import path to the _a11y.scss both for the\n      // source and when this file is distributed. See #10908.\n      @media screen and (-ms-high-contrast: active) {\n        opacity: 0.6;\n      }\n    }\n  }\n\n  .cdk-overlay-dark-backdrop {\n    background: $cdk-overlay-dark-backdrop-background;\n  }\n\n  .cdk-overlay-transparent-backdrop {\n    // Note: as of Firefox 57, having the backdrop be `background: none` will prevent it from\n    // capturing the user's mouse scroll events. Since we also can't use something like\n    // `rgba(0, 0, 0, 0)`, we work around the inconsistency by not setting the background at\n    // all and using `opacity` to make the element transparent.\n    &, &.cdk-overlay-backdrop-showing {\n      opacity: 0;\n    }\n  }\n\n  // Overlay parent element used with the connected position strategy. Used to constrain the\n  // overlay element's size to fit within the viewport.\n  .cdk-overlay-connected-position-bounding-box {\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n\n    // We use `display: flex` on this element exclusively for centering connected overlays.\n    // When *not* centering, a top/left/bottom/right will be set which overrides the normal\n    // flex layout.\n    display: flex;\n\n    // We use the `column` direction here to avoid some flexbox issues in Edge\n    // when using the \"grow after open\" options.\n    flex-direction: column;\n\n    // Add some dimensions so the element has an `innerText` which some people depend on in tests.\n    min-width: 1px;\n    min-height: 1px;\n  }\n\n  // Used when disabling global scrolling.\n  .cdk-global-scrollblock {\n    position: fixed;\n\n    // Necessary for the content not to lose its width. Note that we're using 100%, instead of\n    // 100vw, because 100vw includes the width plus the scrollbar, whereas 100% is the width\n    // that the element had before we made it `fixed`.\n    width: 100%;\n\n    // Note: this will always add a scrollbar to whatever element it is on, which can\n    // potentially result in double scrollbars. It shouldn't be an issue, because we won't\n    // block scrolling on a page that doesn't have a scrollbar in the first place.\n    overflow-y: scroll;\n  }\n}\n\n@mixin cdk-a11y {\n  .cdk-visually-hidden {\n    border: 0;\n    clip: rect(0 0 0 0);\n    height: 1px;\n    margin: -1px;\n    overflow: hidden;\n    padding: 0;\n    position: absolute;\n    width: 1px;\n\n    // Avoid browsers rendering the focus ring in some cases.\n    outline: 0;\n\n    // Avoid some cases where the browser will still render the native controls (see #9049).\n    -webkit-appearance: none;\n    -moz-appearance: none;\n  }\n}\n\n/// Emits the mixin's content nested under `$selector-context` if `$selector-context`\n/// is non-empty.\n/// @param selector-context The selector under which to nest the mixin's content.\n@mixin _cdk-optionally-nest-content($selector-context) {\n  @if ($selector-context == '') {\n    @content;\n  }\n  @else {\n    #{$selector-context} {\n      @content;\n    }\n  }\n}\n\n/// Applies styles for users in high contrast mode. Note that this only applies\n/// to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n/// attribute, however Chrome handles high contrast differently.\n///\n/// @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n///    `white-on-black` or `black-on-white`.\n/// @param encapsulation Whether to emit styles for view encapsulation. Values are:\n///     * `on` - works for `Emulated`, `Native`, and `ShadowDom`\n///     * `off` - works for `None`\n///     * `any` - works for all encapsulation modes by emitting the CSS twice (default).\n@mixin cdk-high-contrast($target: active, $encapsulation: 'any') {\n  @if ($target != 'active' and $target != 'black-on-white' and $target != 'white-on-black') {\n    @error 'Unknown cdk-high-contrast value \"#{$target}\" provided. ' +\n           'Allowed values are \"active\", \"black-on-white\", and \"white-on-black\"';\n  }\n\n  @if ($encapsulation != 'on' and $encapsulation != 'off' and $encapsulation != 'any') {\n    @error 'Unknown cdk-high-contrast encapsulation \"#{$encapsulation}\" provided. ' +\n           'Allowed values are \"on\", \"off\", and \"any\"';\n  }\n\n  // If the selector context has multiple parts, such as `.section, .region`, just doing\n  // `.cdk-high-contrast-xxx #{&}` will only apply the parent selector to the first part of the\n  // context. We address this by nesting the selector context under .cdk-high-contrast.\n  @at-root {\n    $selector-context: #{&};\n\n    @if ($encapsulation != 'on') {\n      .cdk-high-contrast-#{$target} {\n        @include _cdk-optionally-nest-content($selector-context) {\n          @content;\n        }\n      }\n    }\n\n    @if ($encapsulation != 'off') {\n      .cdk-high-contrast-#{$target} :host {\n        @include _cdk-optionally-nest-content($selector-context) {\n          @content;\n        }\n      }\n    }\n  }\n}\n\n// Core styles that enable monitoring autofill state of text fields.\n@mixin cdk-text-field {\n  // Keyframes that apply no styles, but allow us to monitor when an text field becomes autofilled\n  // by watching for the animation events that are fired when they start. Note: the /*!*/ comment is\n  // needed to prevent LibSass from stripping the keyframes out.\n  // Based on: https://medium.com/@brunn/detecting-autofilled-fields-in-javascript-aed598d25da7\n  @keyframes cdk-text-field-autofill-start {/*!*/}\n  @keyframes cdk-text-field-autofill-end {/*!*/}\n\n  .cdk-text-field-autofill-monitored:-webkit-autofill {\n    // Since Chrome 80 we need a 1ms delay, or the animationstart event won't fire.\n    animation: cdk-text-field-autofill-start 0s 1ms;\n  }\n\n  .cdk-text-field-autofill-monitored:not(:-webkit-autofill) {\n    // Since Chrome 80 we need a 1ms delay, or the animationstart event won't fire.\n    animation: cdk-text-field-autofill-end 0s 1ms;\n  }\n\n  // Remove the resize handle on autosizing textareas, because whatever height\n  // the user resized to will be overwritten once they start typing again.\n  textarea.cdk-textarea-autosize {\n    resize: none;\n  }\n\n  // This class is temporarily applied to the textarea when it is being measured. It is immediately\n  // removed when measuring is complete. We use `!important` rules here to make sure user-